// Components
import BaseHeader from '@/components/argon-core/BaseHeader'

import MainTitle from '~/components/shack/MainTitle.vue'
import SectionTitle from '~/components/shack/SectionTitle.vue'
import UploadButton from '~/components/shack/UploadButton.vue'
import { Select, Option } from 'element-ui'

export default {
  layout: 'ShackDash',
  components: {
    BaseHeader,
    MainTitle,
    UploadButton,
    SectionTitle,
    [Select.name]: Select,
    [Option.name]: Option
  },
  async asyncData({ $wellness, error }) {
    let categories = []

    await $wellness.getWellnessCategories().then(({ data }) => {
      categories = data.data
    })

    if (categories.length > 0) {
      return {
        categories,
        wellness_category_id: categories[0].id
      }
    }

    return {
      categories
    }
  },
  data() {
    return {
      loading: false,
      name: '',
      description: '',
      price_per_session: 0,
      start_date: '',
      end_date: '',
      location: '',
      duration: 30,
      weekly: true,
      wellness_category_id: null,
      slots: [{ day: 0, start: '08:00', capacity: 1 }],
      banner_url: null,
      selects: [
        {
          text: 'Sunday',
          value: 0
        },
        { text: 'Monday', value: 1 },
        { text: 'Tuesday', value: 2 },
        { text: 'Wednesday', value: 3 },
        { text: 'Thursday', value: 4 },
        { text: 'Friday', value: 5 },
        { text: 'Saturday', value: 6 }
      ],
      categories: []
    }
  },
  mounted() {
    this.start_date = this.$moment()
      .add(1, 'hour')
      .format('YYYY-MM-DD HH:00:00')
    this.end_date = this.$moment(this.start_date)
      .add(this.duration, 'minutes')
      .format('YYYY-MM-DD HH:mm:ss')

    this.slots[0].day = this.$moment().day()
    this.slots[0].start = this.$moment(this.start_date).format('HH:mm')
  },
  methods: {
    toggleLoading() {
      this.loading = !this.loading
    },
    addSlot() {
      const { start_date } = this
      this.slots.push({
        day: this.$moment(start_date).day(),
        start: this.$moment(start_date).format('HH:mm'),
        capacity: 1
      })
    },
    removeSlot(i) {
      this.slots.splice(i, 1)
    },
    setEndDate() {
      const { duration, start_date } = this
      this.slots[0].day = this.$moment(start_date).day()
      this.slots[0].start = this.$moment(start_date).format('HH:mm')
      this.end_date = this.$moment(start_date)
        .add(duration, 'minutes')
        .format('YYYY-MM-DD HH:mm:ss')
    },
    addWellness() {
      const {
        name,
        description,
        banner_url,
        price_per_session,
        duration,
        slots,
        location,
        start_date,
        end_date,
        weekly,
        wellness_category_id
      } = this

      let slotList = _.sortBy(
        _.map(slots, o => {
          return {
            ...o,
            duration
          }
        }),
        ['day', 'start']
      )

      const payload = {
        name,
        description,
        banner_url,
        location,
        start_date,
        end_date,
        weekly,
        wellness_category_id,
        price_per_session,
        slots: slotList
      }

      if (!banner_url) {
        this.$bvToast.toast(
          'No image provided or please wait for image to complete upload'
        )
      }

      this.toggleLoading()

      this.$wellness
        .addWellnessSession(payload)
        .then(({ data }) => {
          this.$bvToast.toast('Wellness added successfully', {
            variant: 'sucess',
            solid: true
          })

          this.$router.go(-1)
        })
        .catch(({ response }) => {
          this.toggleLoading()
          this.$bvToast.toast(JSON.stringify(response.data.errors))
        })
    }
  }
}
